@media (max-width: 768px) {
  .inventory-table {
    display: block;
    overflow-x: auto;
  }
}

.low-stock {
  color: red;
  font-weight: bold;
}

.filters {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.category-select {
  padding: 8px 12px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.dashboard-container {
    display: flex;
    min-height: 100vh;
  }
  
  .dashboard-nav {
    width: 200px;
    background-color: #f4f4f4;
    padding: 20px;
  }
  
  .dashboard-nav ul {
    list-style: none;
    padding: 0;
  }
  
  .dashboard-nav li {
    margin-bottom: 10px;
  }
  
  .dashboard-nav a {
    text-decoration: none;
    color: #333;
  }
  
  .dashboard-content {
    flex: 1;
    padding: 20px;
  }
  /* Inventory Table */
  .inventory-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
  }
  .search-input,
  .category-select {
    padding: 8px 12px;
    font-size: 14px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  .dashboard-filters {
    display: flex;
    gap: 10px;
    margin-bottom: 15px;
  }

  .inventory-table th,
  .inventory-table td {
    text-align: left;
    padding: 12px 15px;
    border: 1px solid #ddd;
  }

  .inventory-table th {
    background-color: #f4f4f4;
    font-weight: bold;
    color: #1e293b;
  }

  .inventory-table tr:nth-child(even) {
    background-color: #f9f9f9;
  }

  .inventory-table tr:hover {
    background-color: #e2e8f0; /* Highlight row on hover */
    cursor: pointer;
  }


  .upload-button {
    padding: 10px 15px;
    background-color: #38bdf8;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 20px;
  }
  
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .modal-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    width: 80%;
    max-height: 90%;
    overflow-y: auto;
  }
  
  .confirm-button,
  .cancel-button {
    padding: 10px 15px;
    margin: 10px;
    border: none;
    border-radius: 5px;
  }
  
  .confirm-button {
    background-color: #38bdf8;
    color: white;
  }
  
  .cancel-button {
    background-color: #f87171;
    color: white;
  }
  /* Add Item Form */
.add-item-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9fafb;
  margin-top: 15px;
}

.add-item-form input,
.add-item-form select,
.add-item-form button {
  padding: 10px 12px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.add-item-form input:focus,
.add-item-form select:focus {
  outline: none;
  border-color: #38bdf8;
}

.add-item-form button {
  background-color: #38bdf8;
  color: white;
  cursor: pointer;
  border: none;
}

.add-item-form button:hover {
  background-color: #0284c7;
}

/* Update Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 500px;
  max-height: 90%;
  overflow-y: auto;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.modal-content form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.modal-content input,
.modal-content select,
.modal-content button {
  padding: 10px 12px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.modal-content input:focus,
.modal-content select:focus {
  outline: none;
  border-color: #38bdf8;
}

.modal-content button {
  background-color: #38bdf8;
  color: white;
  cursor: pointer;
  border: none;
}

.modal-content button:hover {
  background-color: #0284c7;
}

.modal-actions {
  display: flex;
  justify-content: flex-end; /* Align buttons to the right */
  gap: 10px; /* Added spacing between buttons */
}

.submit-button {
  background-color: #10b981; /* Green */
  color: white;
}

.submit-button:hover {
  background-color: #059669;
}

.cancel-button {
  background-color: #f87171; /* Red */
  color: white;
}

.cancel-button:hover {
  background-color: #e11d48;
}

/* Add/Update Buttons */
.add-item-button,
.update-button {
  padding: 10px 15px;
  font-size: 14px;
  background-color: #38bdf8;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.add-item-button:hover,
.update-button:hover {
  background-color: #0284c7;
}

/* Delete Button */
.delete-button {
  padding: 10px 15px;
  font-size: 14px;
  background-color: #f87171;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.delete-button:hover {
  background-color: #e11d48;
}

.inventory-table td .update-button,
.inventory-table td .delete-button {
  margin-right: 10px; /* Added spacing between buttons */
}
